
<template>
    <div>
      <div id="reg">
        <form id="form-register">
          <div class="m-tab">
	          <div class="left-select">普通账号注册</div>
	        	<div class="right-select"><a href="">已有账号？</a></div>
	        </div>
          <div class="u-input">
	          <label for="name" class="u-label">账&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp号 :&nbsp</label>
		        <input type="text" name="uname"id="uname"placeholder="请输入用户名" minlength="6" maxlength="12"
             class="i-input" v-model="iName"  @blur="vChange" v-on:input="vinp">
		        <span :class="cName?'msg-default':'msg-success'">{{sName}}</span>
	        </div> 
          <div class="u-input">
            <label for="upwd" class="u-label">密&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp码 :&nbsp</label>
            <input type="password" name="upwd"  minlength="6" maxlength="16"id="upwd" placeholder="6-16位密码,区分大小写" 
            class="i-input" v-model="ipwd" v-on:input="vpwd">
            <span :class="cpwd?'msg-default':'msg-success'">{{spwd}}</span>
          </div>
          <div class="u-input">
            <label for="upwd2" class="u-label">确认密码 :&nbsp</label>
            <input type="password" name="upwd" id="upwd2"  minlength="6" maxlength="16"
            placeholder="请再次输入密码" class="i-input" v-model="ipwd2" v-on:input="vpwd2">
            <span :class="cpwd2?'msg-default':'msg-success'">{{spwd2}}</span>
          </div>
          <div class="u-input">
            <label for="phone" class="u-label">手&nbsp;&nbsp机&nbsp号 :&nbsp</label>
            <input type="text" name="phone" id="phone" placeholder="11位手机号" 
            class="i-input" v-model="iphone" v-on:input="vphone">
            <span :class="cphone?'msg-default':'msg-success'">{{sphone}}</span>
          </div>
          <div class="u-input">
            <label for="email" class="u-label">常用邮箱 :&nbsp</label>
            <input type="email" name="email" id="email" placeholder="请输入常用邮箱" 
            class="i-input" v-model="iemail" v-on:input="vemail">
            <span :class="cemail?'msg-default':'msg-success'">{{semail}}</span>
          </div>
          <p></p>
	        <input type="button" class="b-btn" id="bt-register" value="注         册" @click="regi">
        </form>
      </div>
    </div>
</template>
<script>
 export default {
  name: 'Register',
  data () {
    return {
      iName: '',
      sName:"",
      cName:1,
      ipwd:"",
      spwd:"",
      cpwd:1,
      ipwd2:"",
      spwd2:"",
      cpwd2:1,
      pattern:/^(13[0-9]{9})|(18[0-9]{9})|(14[0-9]{9})|(17[0-9]{9})|(15[0-9]{9})$/,
      iphone:"",
      sphone:"",
      cphone:1,
      epattern:/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/,
      iemail:"",
      semail:"",
      cemail:1
    }
  },
  methods:{
        //用户名查询异步请求
    vChange:function(){
      if(this.iName.length>=6){
        this.$http.get("http://127.0.0.1/sg/data/log/check_uname.php",{params:{uname:this.iName}})
      .then(function(res){
         if(res.body.code===201&&this.iName.length>=6){
            this.sName="用户名被占用";
            this.cName=true;
         }else if(res.body.code===200&&this.iName.length>=6){
            this.sName="用户名可用";
            this.cName=false;
         }
      },function(err){
        console.log(err);
      });
    }
    },
    //用户名方法
    vinp:function(){
      if(this.iName.length==0){
          this.sName="用户名不能为空";
          this.cName=true;
        }else if(this.iName.length<6){
          this.sName="用户名不能少于6位";
          this.cName=true;
        }else{
          this.sName="";
          this.cName=false;
        }
    },
    //密码验证方法
    vpwd:function(){
      if(this.ipwd.length==0){
          this.spwd="密码不能为空";
          this.cpwd=true;
        }else if(this.ipwd.length<6){
          this.spwd="密码不能少于6位";
          this.cpwd=true;
        }else{
          this.spwd="密码可用";
          this.cpwd=false;
        }
    },
    //重复密码验证方法
    vpwd2:function(){
       if(this.ipwd2.length==0){
         this.spwd2="密码不能为空";
          this.cpwd2=true;
       }else if(this.ipwd2!=this.ipwd){
          this.spwd2="两次密码输入不一致";
          this.cpwd2=true;
       }else if(this.ipwd2==this.ipwd){
          this.spwd2="密码一致";
          this.cpwd2=false;
       }
    },
    //手机号验证
    vphone:function(){
      if(!this.pattern.test(this.iphone)){
        this.sphone="手机号格式不正确";
        this.cphone=true;
      }else{
        this.sphone="输入正确";
        this.cphone=false;
      }
    },
    //邮箱验证
    vemail:function(){
      if(!this.epattern.test(this.iemail)){
        this.semail="邮箱格式不正确";
        this.cemail=true;
      }else{
        this.semail="邮箱可用";
        this.cemail=false;
      }
    },
    //注册
    regi:function(){
      if(this.cName==false&&this.cpwd==false&&this.cpwd2==false&&this.cphone==false&&this.cemail==false){
        this.$http.post("http://127.0.0.1/sg/data/log/register.php",
        {headers:{'X-Requested-With': 'XMLHttpRequest'}},
	   {params:{uname:this.iName,upwd:this.ipwd,email:this.iemail,phone:this.iphone}})
      .then(function(res){
         if(res.body.code==200){
            alert("注册成功");
            this.$router.push('/Login');
         }
      },function(err){
        console.log(err);
      });
      }else{
        alert('请按要求填写资料');
      }
    }
  }
}
</script>
<style scoped>
  
</style>



