<template>
    <div>
    <Header></Header>
	  <Nav></Nav>
    <Floor></Floor>
    <Footer></Footer>
    </div> 
</template>
<script>

import Nav from '@/components/myHome/Nav.vue'
import Floor from '@/components/myHome/Floor.vue'
import Header from '@/components/Header'
import Footer from '@/components/Footer'


 export default {
  name: 'home',
  components:{
		Nav,
    Floor,
    Header,
  Footer
  },
	watch: {
    // 如果路由有变化，会再次执行该方法
    '$route':'logout'
  },
  data () {
    return {
      msg: ''
    }
  }
}
</script>
<style></style>


