<template>
    <div>
	<Header></Header>
        <div id="filter">

			<div class="screeconbox subject">
			<b class="only">主题</b>
			<ul class="clearfix">
				<li>
					<div><img src="../assets/img/product/detils/2.jpg" alt=""></div>
					<a href="">魔兽</a>
				</li>
				<li>
					<div>
					<img src="../assets/img/product/detils/3.jpg" alt=""></div>
					<a href="">三生三世</a>
				</li>
				<li>
					<div>
					<img src="../assets/img/product/detils/1.jpg" alt=""></div>
					<a href="">权利的游戏</a>
				</li>
			</ul>			
			</div>
			<hr>

			<div class="screeconbox category">
			<b class="only">类别</b>
			<ul id="clear"class="clearfix">
				<li><a href="">服装箱包</a></li>
				<li><a href="">居家生活</a></li>
				<li><a href="">玩具模型</a></li>
				<li><a href="">数码周边</a></li>
			</ul>
			</div>
			<hr>

			<div id="brandul" class="screeconbox">
			<b class="only">品牌</b>
			<ul class="clearfix">
				<li>影时光</li>
				<li>乐立方</li>
				<li>WETA</li>
			</ul>
			</div>
		</div>
        <div id="showtolls">
		<ul class="showselect">
			<li class="curr">推荐</li>
			<li>最新</li>
			<li>好评</li>
			<li>销量</li>
			<li>价格</li>
		</ul>
		</div>
         <div id="product-box">
            <div class="box-cont" v-for="tmp in pic">
			<a href="##">
			<img :src="sr+tmp.pic" alt="">
			<p style="color:black">{{tmp.title}}<br><b>¥{{tmp.price}}</b>
			</p>
			</a>
			</div>
        </div>
       <div class="pages">
		<a href="javascript:void(0);" v-for="(tmp,index) in pnonum" 
		:key="index" v-on:click="page(index)">{{tmp}}</a>
		</div>
<Footer></Footer>
    </div>
</template>
<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'

 export default {
  name: 'Product',
  data () {
    return {
      msg: 'Welcome',
      pic:[],
      sr:'../static/',
	  kw:"",
	  pno:"1",
	  pnonum:[]
    }
  },
  created:function(){
	  this.search();
      this.htt();
  },methods:{
	  search:function(){
		  this.kw=this.$route.params.kw.split("")[1]||"";
		  this.htt();
	  },
	  htt:function(){
		  this.$http.get("http://127.0.0.1/sg/data/04-products/products.php",
	   {params:{kw:this.kw,pno:this.pno}})
      .then(function(res){
         this.pic=res.data.data;
      },function(err){
        console.log(err);
      });
	  },
	  page:function(index){
		  this.pno=(index+1);
//发送请求
      this.htt();
	  }
  },
	watch: {
    // 如果路由有变化，会再次执行该方法
    '$route':'search'
  },
  components:{
  Header,
  Footer}
}
</script>
<style></style>


