<template>
    <div>
	<Header></Header>
        <div id="htm">
	<div id="clear_header">
	<div id="lg_img"></div>
		<div id="goods-img">
			<div id="md">
			<img :src="psr+md[inde]" id="mImg">

			<div id="mask"></div>
			<div id="superMask"></div>
			</div>
				<div id="sm">
				<ul id="icon_list">
					<li v-for="(tmp,index) in sm" :key="index" v-on:mouseover="over(index)">
					   <img :src="psr+tmp">
					</li>
				</ul>
		
				</div>
			</div>
			<div id="goodsbasic">
				<h3>{{laptop.title}}</h3>
				<i>火爆热销付款后2小时发货</i>
			<div id="goods-detail">
				<ul>
					<li class="price-cont">
						<label for="">时光价</label>
						<div class="price">
						&yen <span>{{laptop.price}}</span>
						<div class="vip-box">
						<p>普通会员价</p>
						<p>无折扣</p>
						</div>
						</div>
					</li>
					<li class="serve">
						<label for="">服务</label>
						<p>{{laptop.promise}}</p>
					</li>
				</ul>
			</div>
			<div id="goodsCount">
				<dl class="s_count">
					<dd id="sRegion">
						<strong>数量</strong>
						<div class="num_ctrl">
							<a href="##" class="minus false" @click="minus"></a>
							<input type="text" :value="num">
							<a href="##" class="add" @click="add"></a>
						</div>
					</dd>
				</dl>
			</div>
			<p id="btn_Region">
				<a href="" id="buyNowBtn">立即购买</a>
				<a href="" id="addCartBtn">加入购物车</a>
			</p>
			<div>
			<dl class="share">
				<dd id="shareResgion">
				<label for="">分享</label>
				<a href="" class="weixin"></a>
				<a href="" class="sina"></a>
				<a href="" class="qq"></a>
				</dd>
			</dl>
			</div>
		</div>
	</div>
	</div>

	<div class="p_pic">
	<div class="p_memu">
		<span>商品详情</span>
		<span>用户评论</span>
		<span>售后服务</span>	
	</div>
	<div id="discuss">
		<div class="user_msg">
			<i></i>
			<span></span>
		</div>
		<div class="discuss-v">
			<span></span>
			<span></span>
		</div>
	</div>
	<div id="p_pic">
	<img src="../assets/img/product/detils/p1.jpg" alt="">
	<img src="../assets/img/product/detils/p2.jpg" alt="">
	<img src="../assets/img/product/detils/p3.jpg" alt="">
	<img src="../assets/img/product/detils/p4.jpg" alt="">
	<img src="../assets/img/product/detils/p5.jpg" alt="">
	<img src="../assets/img/product/detils/p6.jpg" alt="">	
	</div>
	</div>
	<Footer></Footer>
    </div>
</template>
<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'

 export default {
  name: 'Detail',
  data () {
    return {
      msg: 'Welcome',
	  lid:24,
	  laptop:[],
	  sm:[],
	  md:[],
	  lg:[],
	  psr:'../static/',
	  inde:0,
	  num:1
    }
  },
  created:function(){
	  this.lid=this.$route.params.lid;
	   this.$http.get("http://127.0.0.1/sg/data/05-product-details/details.php",
	   {params:{lid:this.lid}})
      .then(function(res){
          this.laptop=res.body.laptop;
		  for(let i=0;i<this.laptop.pics.length;i++){
			  this.sm.push(this.laptop.pics[i].sm);
			  this.md.push(this.laptop.pics[i].md);
			  this.lg.push(this.laptop.pics[i].lg);
		  }
		  
      },function(err){
        console.log(err);
      });
  },
  methods:{
	  over:function(index){
		this.inde=index;
	  },
	  add:function(){
		  this.num++;
	  },
	  minus:function(){
		  if(this.num>1){
			  this.num--;
		  }else{
			  this.num=1;
		  }
	  }
  },
  components:{
  Header,
  Footer}
}
</script>
<style></style>


