<template>
    <div>
     <div id="hea">
<!--头部菜单  -->
				<ul id="headbarnav">
					<li><a href="index.html">首页</a></li>
					<li><a href="##">会员</a></li>
					<li><a href="##">发现</a></li>
					<li><a href="##">社区</a></li>	
				</ul>
<!-- 搜索框 -->
			<div id="search">
				<strong id="searchbox">搜索</strong>
				<span>|</span>
				<input type="text" class="no1" id="txtsearch" placeholder="请按名称、品牌等搜索商品" v-model="val">
			
				<router-link :to="'/Product/'+this.val"class="btn-s" id="btn-search" @click="search"><i></i></router-link>
			</div>
<!-- 	登录注册和用户中心-->
			<div id="user_div">
				<div id="shop_cart"></div>
				<div class="headtool" id="btnLogin" :class="hid?'':'hide1'">		
					<router-link :to="'/Login'" id="login">登录</router-link>
					<router-link :to="'/Register'">注册</router-link>
				
				</div>
				<div class="headtool" id="welcomes" :class="hid?'hide1':''"  @click="out">
					<span>hi</span>
					<a href="##" id="uname">{{uname}}</a>
					<router-link :to="'/Home'" id="btnLogout">注销</router-link>
					
				</div>
			</div>
		</div>
		<div id="nav1">
			 <ul id="menunav">
					<li><a href="##">商品分类</a></li>
					<li><a href="##">预售</a></li>
					<li><a href="##">新品</a></li>
					<li><a href="##">热卖</a></li>
					<li><a href="##">特价</a></li>		
				</ul>
				<a href="cart.html">
				<div class="shoppingcart">
					 <img src="../assets/img/1/cart.png" alt="">
					 <span>我的购物车</span><sup>0</sup>
					
				</div>
				 </a>
		</div>
    </div>
</template>
<script>
    export default {
  name: 'Header',
  data () {
    return {
      msg: 'Welcome',
			val:"",
	  uname:"",
	  hid:true	
    }
  },created:function(){
	  this.uname=sessionStorage.getItem("name");
	if(this.uname){
		this.hid=false;
	}
  },
	methods:{
		search:function(){
			this.$router.push('/Product/'+this.val);
		},
		out:function(){
			sessionStorage.removeItem("name");
			this.hid=true;
		}
	}
}
</script>
<style>
</style>


